}
.anime{
  position: relative;
  -webkit-animation: move 5s ease-in;
          animation: move 5s ease-in;

}

@-webkit-keyframes move{
  0%{
    left:0px;
    top:0px;
  }
  100%{
    
    left: 200px;
    top: 0px;
  }
}

@keyframes move{
  0%{
    left:0px;
    top:0px;
  }
  100%{
    
    left: 200px;
    top: 0px;
  }
}

.food{
  position: absolute;
  -webkit-animation: up 5s;
          animation: up 5s;
}

@-webkit-keyframes up{
  0%{
    top:0px;
    left:0px;
  }
  100%{
    top:0px;
    left:300px;
  }
}

@keyframes up{
  0%{
    top:0px;
    left:0px;
  }
  100%{
    top:0px;
    left:300px;
  }
}
